/* Event Tabs / Buttons Styling */
/* Used to show the selected tab a different color */

#active {
    color: #111936 !important;
}

#activeView {
    fill: #111936;
}

#normalView {
    fill: #A9AEB4;
}

#filterTabs {
    height: 30px;
    list-style-type: none;
    list-style: none;
    padding-left: 0;
}

#filterTabs li {
    display: inline-block;
    padding: 10px;
    color: #A9AEB4;
}

#filterTabs li:hover {
    color: #111936
}


#eventsTab {
    width: 85vw;
    height: 5vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

#viewButtons {
    float: right;
}

@import url('https://fonts.cdnfonts.com/css/basier');

#eventsPage {
    font-family: 'Basier Circle', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

#events-header {
    width: 100vw;
}

#eLogo {
    height: 30px;
    width: 30px;
    margin: 25px;
}

#add-button {
    bottom: 30px;
    position: absolute;
    right: 30px;
}

#add-div {
    width: 100%;
    bottom: 0;
}

#select-div {
    display: none;
}

@media (max-width: 1000px) {
    #eventsTab {
        display: none;
    }

    #events-tab-mobile {
        width: 80vw;
        display: flex;
        justify-content: space-between;
    }

    #select-div {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    #select-label {
        padding-right: 5px;
    }

    #select-box {
        border: 0;
        outline: 0;
        background-color: white;
        color: "#111936";
        border-radius: 0.25em;
        box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.2);
        cursor: pointer;

        &::-ms-expand {
            display: none;
        }

        /* Remove focus outline */
        &:focus {
            outline: none;
        }

        /* <option> colors */
        option {
            color: inherit;
            background-color: white;
        }
    }


    #add-button {
        bottom: 3px;
        position: fixed;
        right: 3px;
    }

    #add-div {
        width: 100%;
        bottom: 0;
    }

}